/* arbre généalogique */

.person {
    stroke: steelblue;
    stroke-width: 3px;
}

.extendable {
    fill: lightsteelblue;
}

.non-extendable {
    fill: #fff;
}

#tree-here {
    position: absolute;
    top: -820px; 
    left: 50%; 
    transform: translateX(-50%);
    width: 100%;
    z-index: -1;
}

.node-label {
    font: 12px sans-serif;
}

.link {
    fill: none;
    stroke: #ccc;
    stroke-width: 2px;
}

.tooltip {
    position: absolute;
    font: 12px sans-serif;
    padding: 12px;
    background: rgba(0, 0, 0, 0.8);
    color: #fff;
    border-radius: 2px;
}


/* Icones */

.icon-container {
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    position: absolute;
    margin-top: auto;
    z-index: 0;
}

.icon {
    width: 32px; 
    height: 32px; 
    margin-top: 12px; 
}


/* Informations personnelles */

.tooltip {
    background-color: rgba(31, 31, 31, 0.831);
    border: none;
    padding: 5px;
    font-size: medium;
}

.tooltip .p {
    text-align: center;
}

.tooltip input {
    color: white;
    background-color: rgba(0, 0, 0, 0);
    border: none;
    margin-left: 20px;
    transition: width 0.25s;
    box-sizing : border-box;
    padding: 0;
    font-size: medium;
}

.presentation {
    justify-content: center;
}

#alone {
    display: flex;
    justify-content: center;
    align-items: center;
}

#alone_content {
    text-align: center;
}

#alone_form {
    border-radius: 15px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    background-color: rgba(238, 238, 238, 0.737);
    width: 450px;
    padding: 20px;
    margin-top: 20px; /* Ajoutez un espace au-dessus du formulaire */
}

#alone_form label {
    display: block;
    margin-bottom: 10px;
}

#alone_form input,
#alone_form select {
    width: calc(100% - 200px); /* Ajustez la largeur selon vos besoins */
    padding: 5px;
    font-size: 12px;
    border-radius: 15px;
    border: none;
    background-color: rgba(197, 196, 196, 0.845);
    margin-bottom: 10px;
}

#alone_form h3 {
    text-align: center;
}

#alone_form td:nth-child(2) {
    width: 100%;
    margin-right: 10xp;
    align-items: left;
}

#alone_form td:nth-child(1) {
    text-align: right;
    width: 20%;
}

#alone_form button {
    padding: 10px 20px;
    font-size: 12px;
    border-style: solid;
    border-radius: 50px;
    cursor: pointer;
    background-color: rgba(197, 196, 196, 0.845);
    border-color: #424242;
    border-width: 2px; /* Réduire la largeur de la bordure */
    color:#424242;
}

#alone_form button:hover {
    background-color: rgba(159, 157, 157, 0.653);
  }

#alone_form button:not(:last-child) {
    margin-right: 10px;
}
